import { useState, useEffect } from "react";
import CloseOpen from "../CloseOpenWindow/closeOpenWindow";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
import Packet from "../../../Packet/Packet";
import "./style.css";

function ProblemWithPath({
  setOpenModal,
  game,
  setGameAfterModalClose,
  setAlertMessage,
  setIsDistroyedProblemWithPath,
  isDestroyed,
}) {
  useEffect(() => {
    const timerId1 = setTimeout(() => {
      setIsDistroyedProblemWithPath(true);
    }, 5000);

    const timerId2 = setTimeout(() => {
      setAlertMessage(
        "Jeden z kabel na cest se pokodil a internetov s se rozpadla na dv sti. Mezi stmi te nevede dn dal cesta, proto se paket neme do messenger serveru dostat. \n Je to stejn, jako kdy mstem protk eka a z jedn sti msta do druh vede pouze jeden most. Kdyby se tento most rozbil, tak bychom se nemohli z jedn sti msta do druh dostat. \n S, kter se rozpadla na dv sti je prv takov rozdlen msto a pokozen kabel je rozbit most. Kdy se podv na s, tak zjist, e takovch most je tam vce. Napi do textovho pole, kolik bychom museli do st pidat novch cest, aby v sti nebyl dn most."
      );
      setGameAfterModalClose(game);
      setOpenModal(true);
    }, 6000);
    return () => {
      clearTimeout(timerId1);
      clearTimeout(timerId2);
    };
  }, []);

  return (
    <>
      {!isDestroyed && (
        <Packet
          key="packet4789546"
          content="ahoj"
          from="102.80.143.201"
          to="71.20.12.201"
          path={["102.80.143.201", "10.5.112.134", "71.20.12.201"]}
          color="#666666"
          speed={11}
          repeat={0}
          marginleft={20}
          nodes={[]}
        ></Packet>
      )}
      {isDestroyed && (
        <CloseOpen
          content={
            <InputBox
              setOpenModal={setOpenModal}
              game={game}
              setGameAfterModalClose={setGameAfterModalClose}
              setAlertMessage={setAlertMessage}
            />
          }
        />
      )}
    </>
  );
}

function InputBox({
  setOpenModal,
  game,
  setGameAfterModalClose,
  setAlertMessage,
}) {
  const [count, setCount] = useState("");

  const handleSubmit = () => {
    if (count === "3") {
      setAlertMessage(
        "Perfektn. Je poteba, aby v sti nebyly dn kabely, kter by byly mostem. Jak jsem vidli. Kdy se takov kabel pokod, tak by se nelo napklad dostat do nkterch serveru. Dokonce by se mohlo stt, e by teba pokozen takovho kabelu mohlo odznout od internetu cel msto nebo stt."
      );
      setGameAfterModalClose("noGame");
    } else {
      setAlertMessage(
        "Toto nen sprvn odpov. Potebuje spotat poet cest, kter je nutn pidat, aby v sti nebyly dn mosty."
      );
      setGameAfterModalClose(game);
    }
    setOpenModal(true);
  };
  return (
    <>
      <div className="problem-with-path-container">
        <label>Kolik cest musme pidat:</label>
        <TextField
          id="standard-basic"
          label="poet"
          variant="standard"
          value={count}
          onChange={(e) => setCount(e.target.value)}
        />
        <Button
          variant="outlined"
          onClick={handleSubmit}
          className="check-button"
        >
          ZKONTROLUJ
        </Button>
      </div>
    </>
  );
}

export default ProblemWithPath;
